<template lang="html">
  <div class="">
    <v-layout row justify-center>
      <v-dialog v-model="popup" persistent max-width="740px">
        <v-card>
          <v-card-title>
            <b class="display-1">แก้ไขการนัดหมายที่:</b>
            <v-spacer></v-spacer>
            <p class="text-xs-right"><button @click="popup = false"><v-icon color="black">close</v-icon></button></p>
          </v-card-title>
          <v-container grid-list-xl fill-height>
            <v-layout wrap align-center>
              <v-flex xs12 sm6>
                <v-header v-text="'วันที่ใหม่ :'"></v-header>
              </v-flex>
              <v-flex xs12 sm5>
                  <v-dialog ref="dialog" v-model="modalCalender" :return-value.sync="dateAppoint" persistent lazy full-width width="290px">
                    <v-text-field slot="activator" v-model="dateAppoint" label="วันที่นัดหมายใหม่" append-icon="event" readonly></v-text-field>
                    <v-date-picker v-model="dateAppoint" scrollable>
                      <v-spacer></v-spacer>
                      <v-btn flat color="primary" @click="modalCalender = false">Cancel</v-btn>
                      <v-btn flat color="primary" @click="$refs.dialog.save(dateAppoint)">OK</v-btn>
                    </v-date-picker>
                  </v-dialog>
                </v-flex>
              <v-flex xs12 sm6>
                <v-header v-text="'เวลาใหม่ :'"></v-header>
              </v-flex>
              <!-- <v-flex xs12 sm6>
                <v-text-field placeholder="เวลา"></v-text-field>
              </v-flex> -->
              <v-flex xs12 sm5>
                  <v-menu ref="menu" :close-on-content-click="false" v-model="modalTime" :nudge-right="40" :return-value.sync="time" lazy transition="scale-transition"
                  offset-y full-width max-width="290px" min-width="290px">
                  <v-text-field slot="activator" v-model="timeAppoint" label="เวลาที่นัดหมายใหม่" append-icon="access_time" readonly></v-text-field>
                  <v-time-picker v-if="modalTime" v-model="timeAppoint" @change="$refs.menu.save(timeAppoint)"></v-time-picker>
                  </v-menu>
                </v-flex>

              <v-flex xs12 sm6>
                <v-header v-text="'สาเหตุการเลื่อนนัด :'"></v-header>
              </v-flex>
              <v-flex xs12 sm5>
                <v-text-field  v-model="reason" placeholder="กรุณาระบุสาเหตุการเลื่อนนัด" multi-line></v-text-field>
              </v-flex>
              <v-flex xs12 md8>
              </v-flex>
              <v-flex xs12 md3>
                <v-btn color="primary" @click="submitEditedAppointment()" class="mt-0 mr-0">
                  <v-icon>add</v-icon>
                  <span class="hidden-xs-only">บันทึกการนัดหมาย</span>
                </v-btn>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-dialog>
    </v-layout>

  </div>

</template>

<script>
export default {
  data () {
    return {
      popup: false,
      modalCalender: false,
      dateAppoint: null,
      timeAppoint: null,
      modalTime: false,
      appointId: '',
      reason: '',
      receiveReasonId: '',
      receiveDentistId: '',
      receiveAppointId: '',
      EditedAppointment: []
    }
  },
  created () {
    this.storeId()
  },
  methods: {
    open (id) {
      this.appointId = id
      // console.log('Id', this.appointId)
      this.popup = true
      this.storeId()
    },
    submitEditedAppointment () {
      var textSource = {
        placeId: 1,
        appointId: this.appointId,
        dentistId: 'WT1800014',
        dateAppoint: this.dateAppoint,
        timeAppoint: this.timeAppoint,
        reason: this.reason
      }
      this.axios.post(process.env.API + '/requsetPostponeAppoint', { Source: textSource }).then((response) => {
        // console.log(response.data.Status)
        // console.log('เข้า')
        // console.log(textSource)
        if (response.data.Status === 'Success') {
          this.EditedAppointment = response.data.Result
          this.popup = false
          this.$emit('checkStatus')
          this.$swal({
            title: 'บันทึกการนัดหมายสำเร็จ',
            text: '',
            type: 'success'
          })
          this.clearData()
          this.popup = false
        } else {
          this.$swal('บันทึกการนัดหมายไม่สำเร็จ', 'กรุณาลองใหม่อีกครั้ง หรือ ติดต่อเจ้าหน้าที่', 'error')
          // console.log('ไม่ผ่าน')
        }
      })
    },
    clearData () {
      this.appointId = ''
      this.dentistId = ''
      this.dateAppoint = ''
      this.timeAppoint = []
      this.reason = ''
      this.reasonId = ''
    }
  }
}
</script>

<style lang="css">
</style>



