<template lang="html">
  <v-container>
    <v-layout row wrap>
      <!-- <pre>{{postponementItems}}</pre> -->
      <v-flex xs12 sm6 md10>
        <h1 class="display-1">ตารางคำร้องขอเลื่อนการนัดหมาย</h1>
      </v-flex>
      <v-flex xs12 sm6 md2>
        <v-menu v-model="modal" ref="dialog" :close-on-content-click="false" :nudge-right="40" min-width="295px" transition="scale-transition" offset-y full-width lazy>
          <v-text-field v-model="date" label="ค้นหาวันที่" slot="activator" append-icon="event" readonly required></v-text-field>
          <v-date-picker v-model="date" locale="th" @change="save"></v-date-picker>
        </v-menu>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12>
      <v-data-table :headers="headers"  :items="postponementItems" :pagination.sync="pagination" hide-actions>
        <template slot="items" slot-scope="props">
          <td>{{ props.index + 1 }}</td>
          <td>{{ props.item.topic }}</td>
          <td>{{ props.item.memberFullname }}</td>
          <td>{{ props.item.dentistFullname }}</td>
          <td>{{ props.item.dateAppoint }}</td>
          <td>{{ props.item.timeAppoint }}</td>
          <td class="right">
            <v-btn icon @click="approvePostponementOfAppointment(props.item)"><v-icon color= "green">done</v-icon></v-btn>
          </td>
        </template>
      </v-data-table>
      <div class="text-xs-center pt-2">
        <v-pagination v-model="pagination.page" :length="pages"></v-pagination>
      </div>
        <ModalPostponementOfAppointment ref="popups"/>
    </v-flex>
    </v-layout>

  </v-container>
</template>

<script>
export default {
  data () {
    return {
      headers: [
        { text: '#', sortable: false },
        { text: 'หัวข้อการติดต่อ', sortable: false },
        { text: 'ผู้ติดต่อ', sortable: false },
        { text: 'ทันตแพทย์', sortable: false },
        { text: 'วันที่', sortable: false },
        { text: 'เวลา', sortable: false },
        { text: '', sortable: false }
      ],
      pagination: {},
      postponementItems: [],
      approvePostponement: [],
      date: new Date().toISOString().slice(0, 10),
      appointId: '',
      dentistId: '',
      dateAppoint: '',
      timeAppoint: '',
      reason: '',
      reasonId: '',
      receiveItems: []
    }
  },
  created () {
    this.PostponedTable()
  },
  methods: {
    save (date) {
      this.$refs.dialog.save(date)
      this.PostponedTable()
    },
    PostponedTable () {
      var textSource = {
        placeId: 1,
        dateAppoint: this.date
      }
      this.axios.post(process.env.API + '/showPostponementOfAppointment', { Source: textSource }).then((response) => {
        if (response.data.Status === 'Success') {
          this.postponementItems = response.data.Result
          // console.log('ผ่านนนนนนนนนนน')
        } else {
          // console.log('ไม่ผ่านนนน')
        }
      })
    },
    approvePostponementOfAppointment (item) {
      this.receiveItems = item
      var receiveAppointId = this.receiveItems.appointId
      var receiveDentistId = this.receiveItems.dentistId
      var receiveDateAppoint = this.receiveItems.dateAppoint
      var receiveTimeAppoint = this.receiveItems.timeAppoint
      var receiveReason = this.receiveItems.reason
      var receiveReasonId = this.receiveItems.reasonId
      var textSource = {
        placeId: 1,
        appointId: receiveAppointId,
        dentistId: receiveDentistId,
        dateAppoint: receiveDateAppoint,
        timeAppoint: receiveTimeAppoint,
        reason: receiveReason,
        reasonId: receiveReasonId
      }
      // console.log('textSource', textSource)
      this.axios.post(process.env.API + '/approvePostponementOfAppointment', { Source: textSource }).then((response) => {
        if (response.data.Status === 'Success') {
          this.approvePostponement = response.data.Result
          // console.log('ผ่านนนนนนนนนนน')
          this.PostponedTable()
        } else {
          // console.log('ไม่ผ่านนนน')
        }
      })
    }
  },
  computed: {
    pages () {
      if (this.pagination.rowsPerPage === null || this.postponementItems === []) {
        return 0
      } else {
        return Math.ceil(this.postponementItems.length / this.pagination.rowsPerPage)
      }
    }
  }
}
</script>

<style lang="css" scoped>
</style>



