<template lang="html">
  <v-container pa-4>
    <v-layout row wrap>
      <v-flex xs8 md10>
        <h1 class="display-1 mb-4">จัดการข้อมูลที่เกี่ยวข้อง</h1>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12>
        <v-card>
          <v-container>
            <v-layout row wrap>
              <v-flex xs12 sm4 md2>
                <v-layout row wrap>
                  <v-flex xs8 offset-xs2>
                    <img :src="image" class="w-100 pa-0"/>
                  </v-flex>
                  <v-flex xs8 offset-xs2 class="center-x">
                    <v-btn outline color="primary" class="myLabel w-100 ma-0" @click="">
                      <input type="file" @change="onFileChange"/>
                      <v-icon color="primary">add_photo_alternate</v-icon> เปลี่ยนรูป
                    </v-btn>
                  </v-flex>
                </v-layout>
              </v-flex>
              <v-flex xs12 sm8 md10>
                <v-container grid-list-lg class="grey lighten-5">
                  <v-layout row wrap>
                    <v-flex xs12 md10 py-0 mt-3>
                      <div v-show="!isEditingHospitalName">
                        <h1 class="display-1">{{ hospitalName }}</h1>
                      </div>
                      <div v-show="isEditingHospitalName">
                        <v-text-field v-model="hospitalName" :disabled ="!isEditingHospitalName"></v-text-field>
                      </div>
                    </v-flex>
                    <v-flex text-xs-right xs12 md2>
                      <v-btn fab small color="primary" @click="isEditingHospitalName = !isEditingHospitalName">
                        <v-icon v-if="isEditingHospitalName">close</v-icon>
                        <v-icon v-else>edit</v-icon>
                      </v-btn>
                    </v-flex>
                    <v-flex xs10>
                      <v-text-field v-model="hospitalAddress" color="blue darken-4" multi-line :disabled ="!isEditingAddress">
                        <div slot="label">
                          ที่อยู่
                        </div>
                      </v-text-field>
                    </v-flex>
                    <v-flex text-xs-right xs12 md2>
                      <v-btn fab small color="primary" @click="isEditingAddress = !isEditingAddress">
                        <v-icon v-if="isEditingAddress">close</v-icon>
                        <v-icon v-else>edit</v-icon>
                      </v-btn>
                    </v-flex>
                    <!-- <v-flex xs10>
                      <div v-show="!isEditingAddress">
                        <a href="https://goo.gl/maps/g25PonYKfg22" target="_blank" disabled>
                          6 ซอย โยธี แขวง ทุ่งพญาไท เขต ราชเทวี กรุงเทพมหานคร 10400
                        </a>
                      </div>
                      <div v-show="isEditingAddress">
                        <v-text-field placeholder="https://www.example.com"></v-text-field>
                      </div>
                    </v-flex> -->
                  </v-layout>
                </v-container>
              </v-flex>
            </v-layout>
            <v-layout row wrap class="mt-5">
              <v-flex xs12>
                <h2 class="text-xs-left">วันเวลาที่ทำงาน</h2>
              </v-flex>
              <v-flex xs12>
                <v-data-table :headers="headersprofile" :items="jobList" hide-actions>
                  <template slot="items" slot-scope="props">
                    <td class="text-xs-center">{{ props.item.date }}</td>
                    <td class="text-xs-center">{{ props.item.job_time }}</td>
                    <td class="text-xs-center">{{ props.item.job_endtime }}</td>
                    <td class="text-xs-center">
                      <toggle-button id="changed-font" v-model="status" :width="80" :height="30" :speed="480" :labels="{checked: 'ON', unchecked: 'OFF'}"/>
                    </td>
                  </template>
                </v-data-table>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
    <v-card class="mt-5">
      <v-container grid-list-lg>
        <v-layout row wrap>
          <v-flex xs12 md10>
            <h1 class="display-1">เกี่ยวกับเรา</h1>
          </v-flex>
          <v-flex text-xs-right xs12 md2>
            <v-btn fab small color="primary" @click="isEditTextBox = !isEditTextBox">
              <v-icon v-if="isEditTextBox">close</v-icon>
              <v-icon v-else>edit</v-icon>
            </v-btn>
          </v-flex>
        </v-layout>
        <div>
          <v-flex xs12>
            <v-text-field v-show="!isEditTextBox" v-model="content" :disabled="!isEditTextBox" color="blue darken-4" multi-line></v-text-field>
          </v-flex>
        </div>
        <div>
          <ckeditor v-show="isEditTextBox" v-model="content" :config="config" @blur="onBlur($event)" @focus="onFocus($event)"></ckeditor>
        </div>
      </v-container>
    </v-card>
    <Loading ref="loading"/>
    <ModalAddProfileImg ref="addProfileImg" @profileImg="setEmitProfileImg" @profileImgShow="setEmitProfileImgShow"/>
  </v-container>
</template>

<script src="/path/to/ckeditor.js"></script>
<script>
import Ckeditor from 'vue-ckeditor2'
import ModalAddProfileImg from '@/components/center/Home/Modal/ModalAddProfileImg'
import Loading from '@/components/Loading'
export default {
  components: {
    ModalAddProfileImg, Loading, Ckeditor
  },
  data () {
    return {
      menu: false,
      isEditingHospitalName: null,
      isEditingAddress: null,
      isEditTextBox: null,
      hospitalName: 'โรงพยาบาลทันตกรรมที่หนึ่ง',
      dataProfileImg: '',
      content: 'รายละเอียดที่ต้องการกรอก(ทดลองพิมพ์ข้อมูล ลบได้ถ้าฟีเจอร์นี้โอเคแล้ว)',
      config: {
        toolbar: [
          [ 'Undo', 'Redo', '-', 'Bold', 'Italic', 'Underline', '-', 'JustifyLeft', 'JustifyCenter', 'JustifyRight', 'JustifyBlock', '-' ]
        ],
        autoParagraph: false,
        allowedContent: true,
        removePlugins: 'elementspath',
        height: 300
      },
      search: new Date().toISOString().slice(0, 10),
      pagination: {},
      selected: [],
      status: false,
      hospitalAddress: '',
      image: '/static/img/icons/drawer_icon.png',
      headersprofile: [
        { text: 'วัน', align: 'center', sortable: false },
        { text: 'เวลาเปิด', align: 'center', sortable: false },
        { text: 'เวลาปิด', align: 'center', sortable: false },
        { text: 'สถานะ', align: 'center', sortable: false }
      ],
      jobList: [
        {
          'date': 'จันทร์',
          'job_time': '6:00 น.',
          'job_endtime': '18:00 น.'
        },
        {
          'date': 'อังคาร',
          'job_time': '6:00 น.',
          'job_endtime': '18:00 น.'
        },
        {
          'date': 'พุธ',
          'job_time': '6:00 น.',
          'job_endtime': '18:00 น.'
        },
        {
          'date': 'พฤหัสบดี',
          'job_time': '6:00 น.',
          'job_endtime': '18:00 น.'
        },
        {
          'date': 'ศุกร์',
          'job_time': '6:00 น.',
          'job_endtime': '18:00 น.'
        },
        {
          'date': 'เสาร์',
          'job_time': '6:00 น.',
          'job_endtime': '18:00 น.'
        },
        {
          'date': 'อาทิตย์',
          'job_time': '6:00 น.',
          'job_endtime': '18:00 น.'
        }
      ]
    }
  },
  created () {
    this.dataProfileImg = new FormData()
  },
  methods: {
    onFileChange (e) {
      let files = e.target.files || e.dataTransfer.files
      if (!files.length) return
      let vm = this
      let reader = new FileReader()
      reader.onload = (e) => {
        vm.image = e.target.result
        this.openModalAddProfileImg(vm.image)
      }
      reader.readAsDataURL(files[0])
    },
    setEmitProfileImg (value) {
      this.dataProfileImg.append('file', value)
      this.checkEmitProfileImg()
    },
    setEmitProfileImgShow (value) {
      this.image = value
      this.checkEmitProfileImg()
    },
    checkEmitProfileImg () {
      if (this.dataProfileImg.getAll('file') !== null && this.image !== '/static/img/main/person.jpg') {
        this.$refs.addProfileImg.hide()
      }
    },
    openModalAddProfileImg (img) {
      this.$refs.addProfileImg.open('Add', img)
    },
    openLoading () {
      this.$refs.loading.open()
    },
    hideLoading () {
      this.$refs.loading.hide()
    },
    onBlur (editor) {
      console.log(editor)
    },
    onFocus (editor) {
      console.log(editor)
    }
  }
}
</script>

<style scoped>
.myLabel {
  position: relative;
}
.myLabel:hover input {
  cursor:pointer;
}

.myLabel input {
  position: absolute;
  z-index: 2;
  opacity: 0;
  width: 100%;
  height: 100%;
}
</style>



