<template lang="html">
  <div class="">
    <v-layout row justify-center>
      <v-dialog v-model="modalManage" persistent max-width="740px">
        <v-card>
          <v-card-title>
            <b class="display-1">เพิ่มข้อมูลบริการ (หัตถการ)</b>
            <v-spacer></v-spacer>
            <p class="text-xs-right"><button @click="modalManage = false"><v-icon color="black">close</v-icon></button></p>
          </v-card-title>
          <v-container grid-list-xl fill-height>
            <v-layout wrap align-center>
              <v-flex xs12 sm6>
                <v-header v-text="'ชื่อการให้บริการ :'"></v-header>
              </v-flex>
              <v-flex xs12 sm6>
                <v-text-field v-model="restorationName" label="กรุณากรอกชื่อการให้บริการ" :rules="itemRules.restorationNameRules"></v-text-field>
              </v-flex>
              <v-flex xs12 sm6>
                <v-header v-text="'จำนวนเงิน (บาท) :'"></v-header>
              </v-flex>
              <v-flex xs12 sm6>
                <v-text-field :prefix="currency" v-model="initialBalanceFormatted" placeholder="จำนวนเงิน"></v-text-field>
              </v-flex>
              <v-flex xs12 sm6 md6>
                <v-header v-text="'เวลา (นาที) :'"></v-header>
              </v-flex>
              <v-flex xs12 sm6 md3>
                <v-text-field v-model="timeNumber" type="number" label="กรุณากรอกเวลา" mask="##" :rules="itemRules.timeRules"></v-text-field>
                <!-- <input id="number" type="number" value="42"> -->
              </v-flex>
              <v-flex xs12 sm6 md3>
                <h4>({{ parseInt((timeNumber * 20)/60) }} ชั่วโมง {{ (timeNumber * 20)%60 }} นาที)</h4>
              </v-flex>
              <v-flex xs12 sm6 md6>
                <v-header v-text="'หน่วย :'"></v-header>
              </v-flex>
              <v-flex xs12 sm6 md6>
                <v-text-field v-model="unitName" label="กรุณากรอกหน่วยที่ใช้" :rules="itemRules.unitRules"></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" large @click.native="saveData()">เพิ่ม</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>

<script>
export default {
  data () {
    return {
      modalManage: false,
      restorationName: '',
      timeNumber: 0,
      currency: '฿',
      unitName: '',
      disabled: false,
      initialBalance: null,
      itemRules: {
        restorationNameRules: [
          v => !!v || 'กรุณาระบุข้อมูล',
          v => /^[ก-๙]+$/.test(v) || 'กรุณาระบุข้อมูลเป็นภาษาไทย'
        ],
        timeRules: [
          v => !!v || 'กรุณาใส่หมายเลข',
          v => /^([0-9])+$/.test(v) || 'กรุณากรอกตัวเลข'
        ],
        priceRules: [
          v => !!v || 'กรุณาระบุข้อมูล',
          v => /^([0-9])+$/.test(v) || 'กรุณากรอกตัวเลข'
        ],
        unitRules: [
          v => !!v || 'กรุณาระบุข้อมูล',
          v => /^[ก-๙]+$/.test(v) || 'กรุณาระบุข้อมูลเป็นภาษาไทย'
        ]
      }
    }
  },
  computed: {
    initialBalanceFormatted: {
      get: function () {
        return this.formatAsCurrency(this.initialBalance, 0)
      },
      set: function (newValue) {
        this.initialBalance = Number(newValue.replace(/[^0-9]/g, ''))
      }
    }
  },
  methods: {
    open () {
      this.modalManage = true
    },
    saveData () {
      this.$swal({
        title: 'บันทึกข้อมูลแล้ว',
        text: '',
        type: 'success'
      })
      this.modalManage = false
    },
    formatAsCurrency (value, dec) {
      dec = dec || 0
      if (value === null) {
        return 0
      }
      return '' + value.toFixed(dec).replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1,')
    }
  }
}
</script>

<style lang="css" scoped>
.selectPermission .list__tile__action {
  min-width: 30px !important;
}
</style>



