<template lang="html">
  <v-container pa-4>
    <v-layout row wrap>
      <v-flex xs8 md10>
        <h1 class="display-1">จัดการข้อมูลบริการ (หัตถการ)</h1>
      </v-flex>
      <v-flex xs4 md2 text-xs-right mb-4>
        <v-btn @click="openModal()" color="primary" class="mt-0 mr-0">
          <v-icon>add</v-icon>
          <span class="hidden-xs-only" >เพิ่มข้อมูลบริการ</span>
        </v-btn>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12>
        <v-data-table :headers="headers" :items="listPosition" :pagination.sync="pagination" hide-actions>
          <template slot="items" slot-scope="props">
            <td class="text-xs-center">{{ props.index + 1 }}</td>
            <td class="text-xs-center">{{ props.item.service }}</td>
            <td class="text-xs-center">{{ props.item.cost }}</td>
            <td class="text-xs-center">{{ props.item.minute }}</td>
            <td class="text-xs-center">{{ props.item.unit }}</td>
            <td class="text-xs-center">
              <toggle-button id="changed-font" v-model="status" :width="80" :height="30" :speed="480" :labels="{checked: 'ON', unchecked: 'OFF'}"/>
            </td>
          </template>
        </v-data-table>
        <div class="text-xs-center pt-2">
          <v-pagination v-model="pagination.page" :length="pages"></v-pagination>
        </div>
      </v-flex>
    </v-layout>
    <ModalManageService ref="modalManage"/>
  </v-container>
</template>

<script>
import ModalManageService from '@/components/hospital/ManageService/ModalManageService'
export default {
  components: {
    ModalManageService
  },
  data () {
    return {
      pagination: {},
      status: false,
      headers: [
        { text: '#', align: 'center', sortable: false },
        { text: 'บริการ', align: 'center', sortable: false },
        { text: 'ราคา', align: 'center', sortable: false },
        { text: 'เวลา(นาที)', align: 'center', sortable: false },
        { text: 'หน่วย', align: 'center', sortable: false },
        { text: 'สถานะ', align: 'center', sortable: false }
      ],
      listPosition: [
        {
          'service': 'ขูดหินปูน',
          'cost': 400,
          'minute': 20,
          'unit': 'ครั้ง'
        },
        {
          'service': 'อุดฟัน',
          'cost': 500,
          'minute': 20,
          'unit': 'ซี่'
        },
        {
          'service': 'ถอนฟัน',
          'cost': 400,
          'minute': 20,
          'unit': 'ซี่'
        },
        {
          'service': 'เอ็กซ์เรย์',
          'cost': 1400,
          'minute': 20,
          'unit': 'ครั้ง'
        },
        {
          'service': 'ซีทีสแกน',
          'cost': 1800,
          'minute': 20,
          'unit': 'ครั้ง'
        },
        {
          'service': 'รากเทียม',
          'cost': 8000,
          'minute': 60,
          'unit': 'ซี่'
        },
        {
          'service': 'ครอบฟัน',
          'cost': 7000,
          'minute': 60,
          'unit': 'ซี่'
        }
      ]
    }
  },
  created () {
  },
  watch: {
    'pagination.page': (newPage) => {
      this.onPageChange(newPage)
    }
  },
  computed: {
    pages () {
      if (this.pagination.rowsPerPage == null ||
        this.pagination.totalItems == null
      ) return 0

      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  methods: {
    openModal () {
      this.$refs.modalManage.open()
    }
  }
}
</script>

<style lang="css" scoped>
</style>



